/* @import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap"); */

.footer * {
	margin:0;
	padding:0;
	box-sizing: border-box;
}

.ft-container {
	max-width: 1170px;
	margin:auto;
}

.ft-row {
	display: flex;
	flex-wrap: wrap;
}

.footer > ul {
	list-style: none;
}

.footer {
	background-color: #24262b;
    padding: 70px 0;
}

.ft-col{
   width: 33%;
   padding: 0 15px;
}

.ft-col h4 {
	font-size: 18px;
	color: #ffffff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
}
.ft-col h4::before {
	content: '';
	position: absolute;
	left:0;
	bottom: -10px;
	background-color: #e91e63;
	height: 2px;
	box-sizing: border-box;
	width: 50px;
}

.ft-col ul li:not(:last-child) {
	margin-bottom: 10px;
}

.ft-col ul li a {
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}

/* Optimize  */
.ft-col ul li {
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}

.ft-col ul li a:hover{
	color: #ffffff;
	padding-left: 8px;
}
.ft-col .ft-social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: rgba(255,255,255,0.2);
	margin:0px 10px 10px 0px;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #ffffff;
	transition: all 0.5s ease;
}

.ft-col .ft-social-links a:hover{
	color: #24262b;
	background-color: #ffffff;
}

/*responsive*/
@media(max-width: 767px){
  .footer-col{
    width: 50%;
    margin-bottom: 30px;
}
}
@media(max-width: 574px){
  .footer-col{
    width: 100%;
}
}

.ft-social-icon {
  margin-top: 20%;
  margin-bottom: 20%;
}